<template>
    <v-layout align-start>
        <v-flex>

            <!-- Encabezado -->
            <v-toolbar flat color="white">
                <v-toolbar-title class="primary--text">Marca</v-toolbar-title>
                    <v-divider
                    class="mx-2"
                    inset
                    vertical
                    ></v-divider>

                    <v-spacer></v-spacer>

                    <!-- Busqueda -->
                    <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="Bsqueda" single-line hide-details></v-text-field>
                    <v-spacer></v-spacer> 
                        
                    <!-- Boton Nuevo -->

                         <!-- Ventana Nuevo o Actualizar -->

                    <v-dialog v-model="dialog" max-width="500px">

                         <template v-slot:activator="{ on }"> 
                             <v-btn color="primary" dark class="mb-2" v-on="on">Nuevo</v-btn>                              
                         </template>

                            <!-- Ventana Nuevo o Actualizar -->
                            <v-card>
                                    <!-- Titulo de la ventana(NUEVO O ACTUALIZAR) -->
                                    <v-card-title >
                                    <span class="headline" >{{ formTitle }}</span>
                                    </v-card-title>
                                
                                    <v-card-text>
                                        <v-container grid-list-md>
                                            <v-layout wrap>
                                            <v-flex xs12 sm12 md12>
                                                <v-text-field v-model="nombre" label="Nombre Marca"></v-text-field>
                                            </v-flex>

                                            <v-flex xs12 sm12 md12>
                                                <v-text-field v-model="descripcion" label="Descripcin"></v-text-field>
                                            </v-flex>  

                                            <!-- Muestra msj de validacion de datos -->
                                            <v-flex xs12 sm12 md12 v-show="valida">
                                                <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">
                                                </div>
                                            </v-flex>   

                                            </v-layout>
                                        </v-container>
                                    </v-card-text>

                                     <!-- Opcion Cancelar Guardar -->       
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="primary" text @click.native="close">Cancelar</v-btn>
                                    <!--llama al metodo guardar -->
                                    <v-btn color="primary" text @click.native="guardar">Guardar</v-btn>
                                </v-card-actions>

                            </v-card>
                      
                    </v-dialog>

                    <!--Ventana Activar o Desactivar -->
                    <v-dialog v-model="adModal" max-width="290">
                        <v-card>
                            <v-card-title class="headline" v-if="adAccion==1">Activar Item?</v-card-title>
                            <v-card-title class="headline" v-if="adAccion==2">Desactivar Item?</v-card-title>
                            <v-card-text>
                                Ests a punto de 
                                <span v-if="adAccion==1">Activar </span>
                                <span v-if="adAccion==2">Desactivar </span>
                                el tem {{ adNombre }}
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="green darken-1" text="text" @click="activarDesactivarCerrar">
                                    Cancelar
                                </v-btn>
                                <v-btn v-if="adAccion==1" color="orange darken-4" text="text" @click="activar">
                                    Activar
                                </v-btn>
                                <v-btn v-if="adAccion==2" color="orange darken-4" text="text" @click="desactivar">
                                    Desactivar
                                </v-btn>
                            </v-card-actions>

                        </v-card>
                    </v-dialog>

            </v-toolbar>
                

            <!-- DataTable -->
            <v-data-table
                :headers="headers"
                :items="marcas"
                :search="search"
                class="elevation-1"
            >
              
                <template v-slot:item="props">
                    <tr>
                        <td class="justify-center layout px-0">
                                
                             <!-- Icono Editar o Actualizar -->
                            <v-icon
                                color="black"
                                small
                                class="mr-2"
                                @click="editItem(props.item)" >
                                    edit
                            </v-icon>
                                
                            <!-- Icono Activar Si Esta Desactivada -->
                            <template v-if="props.item.condicion">
                                <v-icon
                                    small
                                    @click="activarDesactivarMostrar(2,props.item)"
                                    >
                                    block
                                </v-icon>
                            </template>

                            <!-- Icono Desactivar Si activada -->
                            <template v-else>
                                <v-icon
                                    small
                                    @click="activarDesactivarMostrar(1,props.item)"
                                    >
                                    check
                                </v-icon>
                            </template>
                                

                            <!-- Columnas del DataTable (Nombre Descripcion) -->  
                            </td>

                                <td>{{ props.item.nombre}}</td>                                                             
                                <td>{{ props.item.descripcion }}</td>  
                            <td >

                            <!-- Columnas del DataTable Mostrar ACTIVO -->  
                            <div v-if="props.item.condicion">
                                <span class="blue--text">Activo</span>
                            </div>

                            <!-- Columnas del DataTable Mostrar INACTIVO -->
                            <div v-else>
                                <span class="red--text">Inactivo</span>
                            </div>
                        </td>
                    </tr>  
                                    
                </template>

                
                    <!-- Opcion resetear si no hay Datos -->           
                <template v-slot:no-data>
                        <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
    
        </v-flex>
    </v-layout>
</template>



<!-- Logica --> 

<script>

    //import axios 
    import axios from 'axios'


    export default {

           // 
        data(){

            return {
               marcas:[],                            
               dialog: false,
               
               headers: [
                    { text: 'Opciones', value: 'actions', sortable: false },                    
                    { text: 'Nombre', value: 'nombre' },
                    { text: 'Descripcin', value: 'descripcion', sortable: false  },
                    { text: 'Estado', value: 'condicion', sortable: false  }                
                ],
                
                search: '',
                editedIndex: -1,

                // variables de Presentacion
                id: '',
                nombre: '',
                descripcion: '',

                valida: 0,       //validar los datos
                validaMensaje:[], //validar el msj

                adModal: 0,
                adAccion: 0,
                adNombre: '',
                adNombres: '',
                adId: ''             
            }


        },




        computed: {
            //evaluar si la variable es 1 o -1 para nuevo y actulizar
            formTitle () {
                return this.editedIndex === -1 ? 'Nueva Marca' : 'Actualizar Marca'
            }
        },

        watch: {
            dialog (val) {
            val || this.close()
            }
        },

        created () {
            this.listar();
        },

        //---METODOS----------------------
        methods:{

            //Listar
            listar(){
                let me=this;

                //autorizacion
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};

                axios.get('api/Marcas/Listar', configuracion).then(function(response){
                    //console.log(response);
                    me.marcas=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
          
            //Editar 
            editItem (item) {
                this.id=  item.idMarca; //valores del DB               
                this.nombre=item.nombre;
                this.descripcion=item.descripcion;
                this.editedIndex=1;
                this.dialog = true
                this.valida= 0;
            },

            deleteItem (item) {
                const index = this.desserts.indexOf(item)
                confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
            },

            //metodo cerrar
            close () {
                this.dialog = false;
                this.limpiar();
            },

            //metodo limpiar
            limpiar(){
                this.id="";
                this.nombre="";
                this.descripcion="";
                this.editedIndex=-1;
            },

            //metodo guardar
            guardar () {
                if (this.validar()){
                    return;
                }

                //autorizacion
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};

                if (this.editedIndex > -1) {

                    //Cdigo para editar
                    
                    let me=this;
                    //Peticion ajax mediante axios
                    axios.put('/api/Marcas/Actualizar',{
                        'idMarca':me.id,
                        'nombre': me.nombre,
                        'descripcion': me.descripcion

                    },configuracion).then(function(response){
                        //si todo funciona correctamente llama al metodo response
                        me.close();
                        me.listar();
                        me.limpiar();                        
                    }).catch(function(error){
                        console.log(error);
                    });

                } else {
                    //Cdigo para guardar
                    let me=this;
                    axios.post('/api/Marcas/Crear',{
                        'Nombre': me.nombre,                       
                        'Descripcion': me.descripcion
                    },configuracion).then(function(response){
                        me.close();
                        me.listar();
                        me.limpiar();                        
                    }).catch(function(error){
                        console.log(error);
                    });
                }
            },

            //metodo validar
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if (this.nombre.length<1 || this.nombre.length>100){
                    this.validaMensaje.push("Favor llenar el campo Nombre Marca");
                }

                if (this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },


            //metodo Activar o desactivar
            activarDesactivarMostrar(accion,item){
                this.adModal=1; 
                this.adNombre=item.nombre; //valor de la BD
                this.adId=item.idMarca;  //valor de la BD              
                if (accion==1){
                    this.adAccion=1;
                }
                else if (accion==2){
                    this.adAccion=2;
                }
                else{
                    this.adModal=0;
                }
            },
            activarDesactivarCerrar(){
                this.adModal=0;
            },
            activar(){
                let me=this;

                //autorizacion
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};

                axios.put('/api/Marcas/Activar/'+this.adId,{},configuracion).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                  
                    me.adId="";
                    me.listar();                       
                }).catch(function(error){
                    console.log(error);
                });
            },
            desactivar(){
                let me=this;
                //autorizacion
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.put('/api/Marcas/Desactivar/'+this.adId,{},configuracion).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;                 
                    me.adId="";
                    me.listar();                       
                }).catch(function(error){
                    console.log(error);
                });
            }


        }        
    }






</script>




